

/* Mobile Styles */
@media only screen and (max-width: 960px) {
        #rsvp-page {
            position: absolute;
            width: 100%;
            height: 100%;
            -ms-flex-pack: distribute;
                justify-content: space-around;
            -ms-flex-line-pack: center;
                align-content: center;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            background-color: rgba(125, 81, 109, 1.0); /* Cosmic */;
        }
    }
    
    /* Desktop Styles */
    @media only screen and (min-width: 961px) {
        #rsvp-page {
            width: 100%;
            height: 100%;
            position: absolute;
            background: url("../images/rsvp.jpg") no-repeat center center fixed;
            background-size: cover;
            -ms-flex-pack: distribute;
                justify-content: space-around;
            -ms-flex-line-pack: center;
                align-content: center;
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: row;
                flex-direction: row;
            font-family: 'Source Sans Pro', sans-serif;
        }
    
    }


