/* Mobile Styles */
@media only screen and (max-width: 960px) {
  #home-page {
      background-image: none;
      background-color: #B9CCB8;
  }
}

/* Desktop Styles */
@media only screen and (min-width: 961px) {
    #home-page {
        width: 100%;
        height: 100%;
        position: absolute;
        background: url("../images/home.jpg") no-repeat center center fixed;
        background-size: cover;
    }
}


